<template>
  <base-layout
    :white-background="$vuetify.breakpoint.mobile"
    no-shadow
  >
    <template #header>
      <button-back :to="{ name: $constants.ROUTE_NAMES.LOADS }">
        Loads
      </button-back>

      <v-col
        class="py-0 ml-5"
      >
        <h2 class="primary-dark--text font-weight-semi-bold">
          General Information
        </h2>
      </v-col>

      <v-col class="d-flex flex-column flex-sm-row justify-sm-end pa-0">
        <div class="mr-sm-2 mr-md-10 mb-2 mb-sm-0">
          <v-btn
            depressed
            color="primary"
            min-width="130"
            @click="submitCreateForm"
            :loading="isCreateLoadButtonLoading"
          >
            Create
          </v-btn>
        </div>
      </v-col>
    </template>
    <template #content>
      <v-container
        class="pb-16"
        style="padding: 30px 30px"
        fluid
      >
        <v-row class="row px-2">
          <v-col
            class="order-1 order-lg-0"
            cols="12"
            sm="12"
            md="12"
            lg="8"
          >
            <v-row id="general">
              <v-col>
                <v-form
                  ref="generalInformationForm"
                  v-model="generalInformationForm.valid"
                  lazy-validation
                >
                  <v-row>
                    <v-col
                      class="py-0"
                      cols="12"
                      md="6"
                    >
                      <v-card
                        class="form__card"
                      >
                        <base-field-input
                          label="Load ID"
                          v-model="generalInformationForm.values.load_id"
                          :rules="[cyrillic]"
                          @input="checkId"
                          :color="
                            generalInformationForm.loadId.loading ? 'primary' :
                            !generalInformationForm.loadId.invalid ? 'success' :
                            !generalInformationForm.values.load_id ? '' :
                            generalInformationForm.loadId.invalid && generalInformationForm.loadId.checked ? 'error' : 'primary'
                          "
                          :error="Boolean(generalInformationForm.loadId.error)"
                          :error-messages="generalInformationForm.loadId.error"
                        >
                          <template #append>
                            <v-progress-circular
                              v-if="generalInformationForm.loadId.loading"
                              class="full-width"
                              :size="23"
                              :width="3"
                              style="width: 20px;"
                              color="primary"
                              indeterminate
                            />

                            <v-icon
                              v-else-if="generalInformationForm.loadId.checked && generalInformationForm.values.load_id && generalInformationForm.valid"
                              :color="generalInformationForm.loadId.invalid ? 'error' : 'success'"
                              size="23"
                            >
                              {{ generalInformationForm.loadId.invalid ? 'mdi-information' : 'mdi-check' }}
                            </v-icon>
                          </template>
                        </base-field-input>
                        <base-field-autocomplete
                          label="Driver"
                          v-model="generalInformationForm.values.driver"
                          :items="drivers.data"
                          item-text="full_name"
                          item-value="id"
                        />

                        <base-field-autocomplete
                          label="Dispatcher"
                          v-model="generalInformationForm.values.dispatcher"
                          :items="dispatchers.data"
                          item-text="name"
                          item-value="id"
                        />
                      </v-card>
                    </v-col>

                    <v-col
                      class="py-0"
                      cols="12"
                      md="6"
                    >
                      <v-card
                        class="form__card"
                      >
                        <base-field-textarea
                          rows="7"
                          row-height="20"
                          label="Driver's Instructions"
                          v-model="generalInformationForm.values.driver_instructions"
                        />
                      </v-card>
                    </v-col>
                  </v-row>
                </v-form>
              </v-col>
            </v-row>
            <v-row>
              <v-col
                id="pickup"
                cols="12"
                lg="6"
              >
                <v-card
                  class="form__card"
                >
                  <v-row>
                    <v-col
                      class="py-0"
                      cols="12"
                    >
                      <h2 class="primary-dark--text font-weight-semi-bold">
                        Pickup Information
                      </h2>
                    </v-col>
                    <v-col>
                      <v-form
                        ref="pickUpInformationForm"
                        v-model="pickUpInformationForm.valid"
                        lazy-validation
                      >
                        <base-field-input
                          label="Company name"
                          :is-required="true"
                          v-model="pickUpInformationForm.values.pickup_company_name"
                          :rules="[required('Company name')]"
                        />

                        <base-field-input
                          label="Street address"
                          :is-required="true"
                          v-model="pickUpInformationForm.values.pickup_address"
                          :rules="[required('Street address')]"
                        />

                        <base-field-input
                          label="City"
                          :is-required="true"
                          v-model="pickUpInformationForm.values.pickup_city"
                          :rules="[required('City'), minLength(2, 'City')]"
                        />

                        <base-field-autocomplete
                          label="State"
                          :is-required="true"
                          v-model="pickUpInformationForm.values.pickup_state"
                          :items="states.data"
                          item-text="name"
                          item-value="id"
                          :rules="[required('Pickup state')]"
                        />

                        <base-field-input
                          type="string"
                          label="Zip"
                          :is-required="true"
                          v-model="pickUpInformationForm.values.pickup_zip"
                          v-mask="['#####']"
                          :rules="[required('Pickup zip')]"
                        />

                        <base-field-input
                          label="Contact name"
                          :is-required="true"
                          v-model="pickUpInformationForm.values.pickup_contact_name"
                          :rules="[required('Contact name'), alpha('Contact name')]"
                        />

                        <base-field-input
                          label="Phone"
                          :is-required="true"
                          v-model="pickUpInformationForm.values.pickup_phone"
                          mask="+1 (###) ###-####"
                          :rules="[required('Phone')]"
                          validate-on-blur
                        />

                        <base-field-input
                          label="Email"
                          autocomplete="user-email"
                          type="email"
                          v-model="pickUpInformationForm.values.pickup_email"
                          :rules="[email]"
                          validate-on-blur
                        />

                        <base-field-datepicker
                          label="Pickup scheduled for"
                          v-model="pickUpInformationForm.values.scheduled_pickup_date"
                        />

                        <base-field-input
                          label="Buyer number"
                          v-model="pickUpInformationForm.values.buyer_number"
                          :rules="[decimal('Buyer number')]"
                        />

                        <base-field-textarea
                          rows="3"
                          row-height="20"
                          label="Notes"
                          v-model="pickUpInformationForm.values.pickup_notes"
                        />
                      </v-form>
                    </v-col>
                  </v-row>
                </v-card>
              </v-col>
              <v-col
                id="delivery"
                cols="12"
                lg="6"
              >
                <v-card
                  class="form__card"
                >
                  <v-row>
                    <v-col
                      class="py-0"
                      cols="12"
                    >
                      <h2 class="primary-dark--text font-weight-semi-bold">
                        Delivery Information
                      </h2>
                    </v-col>
                    <v-col>
                      <v-form
                        ref="deliveryInformationForm"
                        v-model="deliveryInformationForm.valid"
                        lazy-validation
                      >
                        <base-field-input
                          label="Company name"
                          :is-required="true"
                          v-model="deliveryInformationForm.values.delivery_company_name"
                          :rules="[required('Company name')]"
                        />

                        <base-field-input
                          label="Street address"
                          :is-required="true"
                          v-model="deliveryInformationForm.values.delivery_address"
                          :rules="[required('Street address')]"
                        />

                        <base-field-input
                          label="City"
                          :is-required="true"
                          v-model="deliveryInformationForm.values.delivery_city"
                          :rules="[required('City'), minLength(2, 'City')]"
                        />

                        <base-field-autocomplete
                          label="State"
                          :is-required="true"
                          v-model="deliveryInformationForm.values.delivery_state"
                          :items="states.data"
                          item-text="name"
                          item-value="id"
                          :rules="[required('State')]"
                        />

                        <base-field-input
                          type="string"
                          label="Zip"
                          v-model="deliveryInformationForm.values.delivery_zip"
                          v-mask="['#####']"
                        />

                        <base-field-input
                          label="Contact name"
                          :is-required="true"
                          v-model="deliveryInformationForm.values.delivery_contact_name"
                          :rules="[required('Contact name'), alpha('Contact name')]"
                        />

                        <base-field-input
                          label="Phone"
                          :is-required="true"
                          v-model="deliveryInformationForm.values.delivery_phone"
                          mask="+1 (###) ###-####"
                          :rules="[required('Phone')]"
                          validate-on-blur
                        />

                        <base-field-input
                          label="Email"
                          autocomplete="user-email"
                          type="email"
                          v-model="deliveryInformationForm.values.delivery_email"
                          :rules="[email]"
                          validate-on-blur
                        />

                        <base-field-datepicker
                          label="Delivered scheduled for"
                          v-model="deliveryInformationForm.values.scheduled_delivery_date"
                        />

                        <base-field-textarea
                          rows="3"
                          row-height="20"
                          label="Notes"
                          v-model="deliveryInformationForm.values.delivery_notes"
                        />
                      </v-form>
                    </v-col>
                  </v-row>
                </v-card>
              </v-col>
            </v-row>
            <v-row>
              <v-col
                id="payment"
                cols="12"
                lg="6"
              >
                <v-card
                  class="form__card"
                >
                  <v-row>
                    <v-col
                      class="py-0"
                      cols="12"
                    >
                      <h2 class="primary-dark--text font-weight-semi-bold">
                        Payment Information
                      </h2>
                    </v-col>

                    <v-col>
                      <v-form
                        ref="paymentInformationForm"
                        v-model="paymentInformationForm.valid"
                        lazy-validation
                      >
                        <base-field-autocomplete
                          label="Payment method"
                          :is-required="true"
                          v-model="paymentInformationForm.values.payment_method"
                          :items="paymentMethods.data"
                          item-text="name"
                          item-value="id"
                          :rules="[required('Payment method')]"
                        />

                        <base-field-input
                          type="string"
                          prefix="$"
                          label="Carrier fee"
                          :is-required="true"
                          v-model="paymentInformationForm.values.carrier_fee"
                          :rules="[
                            required('Carrier fee'),
                            decimal('Carrier fee')
                          ]"
                          @keyup="() => hanldePriceInput('paymentInformationForm', 'carrier_fee')"
                        />

                        <base-field-input
                          type="string"
                          prefix="$"
                          label="Broker fee"
                          v-model="paymentInformationForm.values.broker_fee"
                          :rules="[required('Broker fee'), decimal('Broker fee')]"
                          @keyup="() => hanldePriceInput('paymentInformationForm', 'broker_fee')"
                        />

                        <base-field-input
                          type="string"
                          prefix="$"
                          label="Payment on delivery"
                          v-model="paymentInformationForm.values.delivery_payment"
                          @input="() => handlePaymentInput('pickup')"
                          :rules="[decimal('Payment on delivery')]"
                          @keyup="() => hanldePriceInput('paymentInformationForm', 'delivery_payment')"
                        />

                        <base-field-input
                          type="string"
                          prefix="$"
                          label="Payment on pickup"
                          v-model="paymentInformationForm.values.pickup_payment"
                          @input="() => handlePaymentInput('delivery')"
                          :rules="[decimal('Payment on pickup')]"
                          @keyup="() => hanldePriceInput('paymentInformationForm', 'pickup_payment')"
                        />

                        <base-field-textarea
                          rows="4"
                          row-height="20"
                          label="Payment terms"
                          v-model="paymentInformationForm.values.payment_terms"
                        />
                      </v-form>
                    </v-col>
                  </v-row>
                </v-card>
              </v-col>
              <v-col
                id="shipper"
                cols="12"
                lg="6"
              >
                <v-card
                  class="form__card"
                >
                  <v-row>
                    <v-col
                      class="py-0"
                      cols="12"
                    >
                      <h2 class="primary-dark--text font-weight-semi-bold">
                        Shipper/Customer
                      </h2>
                    </v-col>
                    <v-col>
                      <v-form
                        ref="shipperCastomerInformationForm"
                        v-model="shipperCastomerInformationForm.valid"
                        lazy-validation
                      >
                        <base-field-input
                          label="Company name"
                          :is-required="true"
                          v-model="shipperCastomerInformationForm.values.customer_company_name"
                          :rules="[required('Pickup company name')]"
                        />

                        <base-field-input
                          label="Street Address"
                          :is-required="true"
                          v-model="shipperCastomerInformationForm.values.customer_address"
                          :rules="[required('Street address')]"
                        />

                        <base-field-input
                          label="City"
                          :is-required="true"
                          v-model="shipperCastomerInformationForm.values.customer_city"
                          :rules="[required('City'), minLength(2, 'City')]"
                        />

                        <base-field-autocomplete
                          label="State"
                          :is-required="true"
                          v-model="shipperCastomerInformationForm.values.customer_state"
                          :items="states.data"
                          item-text="name"
                          item-value="id"
                          :rules="[required('State')]"
                        />

                        <base-field-input
                          type="string"
                          label="Zip"
                          v-model="shipperCastomerInformationForm.values.customer_zip"
                          v-mask="['#####']"
                          :rules="[decimal('Zip')]"
                        />

                        <base-field-input
                          label="Contact Name"
                          v-model="shipperCastomerInformationForm.values.customer_contact_name"
                          :rules="[alpha('Contact name')]"
                        />

                        <base-field-input
                          label="Phone"
                          :is-required="true"
                          v-model="shipperCastomerInformationForm.values.customer_phone"
                          mask="+1 (###) ###-####"
                          :rules="[required('Phone')]"
                          validate-on-blur
                        />

                        <base-field-input
                          v-model="shipperCastomerInformationForm.values.customer_email"
                          label="Email"
                          autocomplete="user-email"
                          type="email"
                          :rules="[email]"
                          validate-on-blur
                        />

                        <base-field-input
                          label="Fax"
                          v-model="shipperCastomerInformationForm.values.customer_fax"
                        />
                      </v-form>
                    </v-col>
                  </v-row>
                </v-card>
              </v-col>
            </v-row>
            <v-card
              class="form__card"
            >
              <v-row id="vehicles">
                <v-col
                  class="py-0 align-center"
                  cols="12"
                >
                  <h2 class="primary-dark--text font-weight-semi-bold d-inline-flex mr-4">
                    Vehicles
                  </h2>

                  <v-checkbox
                    class="ma-0 d-inline-flex"
                    v-model="pickUpInformationForm.values.enclosed"
                    label="This load requires enclosed trailer type"
                  />
                </v-col>
                <v-col>
                  <v-form
                    ref="vehiclesForm"
                    v-model="vehiclesForm.valid"
                    lazy-validation
                  >
                    <v-simple-table class="table mb-2">
                      <thead v-if="vehiclesForm.values.length">
                        <tr>
                          <th class="text-left pa-1">
                            INOP
                          </th>
                          <th class="pa-1">
                            Year
                          </th>
                          <th class="pa-1">
                            Make
                          </th>
                          <th class="pa-1">
                            Model
                          </th>
                          <th class="pa-1">
                            Type
                          </th>
                          <th class="pa-1">
                            Color
                          </th>
                          <th class="pa-1">
                            Plate
                          </th>
                          <th class="pa-1">
                            VIN
                          </th>
                          <th class="pa-1">
                            Lot number
                          </th>
                          <th class="pa-1">
                            Additional info
                          </th>
                          <th></th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr
                          v-for="(row, index) in vehiclesForm.values"
                          :key="index"
                        >
                          <td class="pa-1">
                            <v-checkbox
                              :name="`vehiclesForm.values[${index}].fields[inop]`"
                              class="pa-0 pt-2 ma-0"
                              v-model="row.fields.inop"
                              type="checkbox"
                              hide-details
                            />
                          </td>
                          <td class="pa-1 pt-2">
                            <base-field-input
                              type="string"
                              :name="`vehiclesForm.values[${index}].fields[year]`"
                              class="fields-year"
                              hide-details
                              :rules="[required('Year'), decimal('Year')]"
                              v-model.number="row.fields.year"
                            />
                          </td>
                          <td class="pa-1 pt-2 make-col">
                            <base-field-autocomplete
                              :key="`vehiclesForm.values[${index}].fields[make]`"
                              :name="`vehiclesForm.values[${index}].fields[make]`"
                              hide-details
                              no-filter
                              hide-selected
                              v-model="row.fields.make"
                              :items="row.autocompletes.makes.data"
                              :value="row.autocompletes.makes.value"
                              item-text="name"
                              item-value="id"
                              :infinite-scroll="Boolean(row.autocompletes.makes.next_page_url)"
                              :append-outer-icon="!row.autocompletes.makes.data.length && row.autocompletes.makes.search ? 'mdi-plus' : undefined"
                              :rules="[required('Make')]"
                              @input="(value) => handleVehicleMakeChange(index, value)"
                              @update:search-input="(event) => handleMakesAutocompleteChange(event, index)"
                              @focus="(event) => handleMakesAutocompleteChange('', index)"
                              @onIntersect="getNextPortionMakes(index)"
                              @keydown.enter="addNewMake(index)"
                              @click:append-outer="addNewMake(index)"
                            />
                          </td>
                          <td class="pa-1 pt-2 model-col">
                            <base-field-autocomplete
                              :ref="`vehicleModel${index}`"
                              :key="`vehiclesForm.values[${index}].fields[model]`"
                              :name="`vehiclesForm.values[${index}].fields[model]`"
                              hide-details
                              no-filter
                              hide-selected
                              v-model="row.fields.model"
                              :disabled="!vehiclesForm.values[index].fields.make"
                              :items="row.autocompletes.models.data"
                              :value="row.autocompletes.models.value"
                              item-text="name"
                              item-value="id"
                              :infinite-scroll="Boolean(row.autocompletes.models.next_page_url)"
                              :append-outer-icon="!row.autocompletes.models.data.length && row.autocompletes.models.search ? 'mdi-plus' : undefined"
                              :search-input.sync="row.autocompletes.models.search"
                              :rules="[required('Model')]"
                              @input="(value) => handleVehicleModelChange(index, value)"
                              @update:search-input="(event) => handleModelsAutocompleteChange(event, index)"
                              @focus="(event) => handleModelsAutocompleteChange('', index)"
                              @onIntersect="getNextPortionModels(index)"
                              @keydown.enter="addNewModel(index)"
                              @click:append-outer="addNewModel(index)"
                            />
                          </td>
                          <td class="pa-1 pt-2">
                            <base-field-autocomplete
                              :ref="`vehicleType${index}`"
                              :name="`vehiclesForm.values[${index}].fields[type]`"
                              class="fields fields-type"
                              v-model="row.fields.type"
                              :items="vehicles.data"
                              item-text="name"
                              item-value="id"
                              hide-details
                              @input="(value) => handleVehicleTypeChange(index, value)"
                              :rules="[required('Type')]"
                            />
                          </td>
                          <td class="pa-1 pt-2">
                            <base-field-input
                              :name="`vehiclesForm.values[${index}].fields[color]`"
                              class="fields fields-color"
                              hide-details
                              v-model="row.fields.color"
                            />
                          </td>

                          <td class="pa-1 pt-2">
                            <base-field-input
                              :name="`vehiclesForm.values[${index}].fields[plate]`"
                              class="fields fields-plate"
                              hide-details
                              v-model="row.fields.plate"
                            />
                          </td>

                          <td class="pa-1 pt-2">
                            <base-field-input
                              :name="`vehiclesForm.values[${index}].fields[vin]`"
                              class="fields fields-vin"
                              hide-details
                              v-model="row.fields.vin"
                              :maxlength="17"
                              :rules="[alphanumeric('VIN'), lengthValidator(1, 17, 'VIN')]"
                            />
                          </td>
                          <td class="pa-1 pt-2">
                            <base-field-input
                              :name="`vehiclesForm.values[${index}].fields[lot]`"
                              class="fields fields-lot"
                              hide-details
                              v-model="row.fields.lot"
                            />
                          </td>

                          <td class="pa-1 pt-2">
                            <base-field-textarea
                              :name="`vehiclesForm.values[${index}].fields[additional_info]`"
                              class="fields fields-additional-info"
                              hide-details
                              rows="1"
                              v-model="row.fields.additional_info"
                            />
                          </td>

                          <td class="pa-0 pt-1">
                            <v-btn
                              icon
                              @click="() => handleVehicleDelete(row.fields.id, index)"
                            >
                              <v-icon color="color-red">
                                mdi-delete-outline
                              </v-icon>
                            </v-btn>
                          </td>
                        </tr>
                      </tbody>
                    </v-simple-table>
                  </v-form>
                  <v-btn
                    class="primary--text"
                    depressed
                    width="130"
                    @click="onAddRowVehiclesTable"
                  >
                    Add vehicles
                  </v-btn>
                </v-col>
              </v-row>
            </v-card>
          </v-col>
          <v-col
            class="order-0 order-lg-1"
            cols="12"
            sm="12"
            md="12"
            lg="4"
          >
            <v-row class="fill-height">
              <v-col
                class="py-0 fill-height"
                cols="12"
              >
                <div class="bookmarks-wrap">
                  <panel-dropdown
                    class="mb-10"
                    title="Bookmarks"
                    expand
                  >
                    <ul class="px-5 py-3">
                      <li>
                        <v-btn
                          class="btn-no-bg text-capitalize pa-0 primary--text justify-start"
                          depressed
                          text
                          left
                          :ripple="false"
                          @click="onGoToBookmark('#general')"
                        >
                          <v-img
                            class="mr-4"
                            :src="require('@images/icons/loads/general.svg')"
                            width="20"
                          />
                          <span :class="{ 'error--text': !generalInformationForm.valid }">
                            General Info
                          </span>
                          <v-img
                            v-if="!generalInformationForm.valid"
                            class="ml-3"
                            :src="require('@images/icons/form/error.svg')"
                            width="20"
                          />
                        </v-btn>
                      </li>

                      <li>
                        <v-btn
                          class="btn-no-bg text-capitalize pa-0 primary--text justify-start"
                          depressed
                          text
                          :ripple="false"
                          @click="onGoToBookmark('#pickup')"
                        >
                          <v-img
                            class="mr-4"
                            :src="require('@images/icons/loads/pickup.svg')"
                            width="20"
                          />
                          <span :class="{ 'error--text': !pickUpInformationForm.valid }">
                            Pickup Info
                          </span>
                          <v-img
                            v-if="!pickUpInformationForm.valid"
                            class="ml-3"
                            :src="require('@images/icons/form/error.svg')"
                            width="20"
                          />
                        </v-btn>
                      </li>

                      <li>
                        <v-btn
                          class="btn-no-bg text-capitalize pa-0 primary--text justify-start"
                          depressed
                          text
                          :ripple="false"
                          @click="onGoToBookmark('#delivery')"
                        >
                          <v-img
                            class="mr-4"
                            :src="require('@images/icons/loads/delivery.svg')"
                            width="20"
                          />
                          <span :class="{ 'error--text': !deliveryInformationForm.valid }">
                            Delivery Info
                          </span>
                          <v-img
                            v-if="!deliveryInformationForm.valid"
                            class="ml-3"
                            :src="require('@images/icons/form/error.svg')"
                            width="20"
                          />
                        </v-btn>
                      </li>

                      <li>
                        <v-btn
                          class="btn-no-bg text-capitalize pa-0 primary--text justify-start"
                          depressed
                          text
                          :ripple="false"
                          @click="onGoToBookmark('#payment')"
                        >
                          <v-img
                            class="mr-4"
                            :src="require('@images/icons/loads/payment.svg')"
                            width="20"
                          />
                          <v-btn
                            class="btn-no-bg text-capitalize pa-0 primary--text justify-start"
                            depressed
                            text
                            :ripple="false"
                            @click="onGoToBookmark('#vehicles')"
                          >
                            <span :class="{ 'error--text': !paymentInformationForm.valid }">
                              Payment Info
                            </span>
                            <v-img
                              v-if="!paymentInformationForm.valid"
                              class="ml-3"
                              :src="require('@images/icons/form/error.svg')"
                              width="20"
                            />
                          </v-btn>
                        </v-btn>
                      </li>

                      <li>
                        <v-btn
                          class="btn-no-bg text-capitalize pa-0 primary--text justify-start"
                          depressed
                          text
                          :ripple="false"
                          @click="onGoToBookmark('#shipper')"
                        >
                          <v-img
                            class="mr-4"
                            :src="require('@images/icons/loads/shipper.svg')"
                            width="20"
                          />
                          <span :class="{ 'error--text': !shipperCastomerInformationForm.valid }">
                            Shipper Info
                          </span>
                          <v-img
                            v-if="!shipperCastomerInformationForm.valid"
                            class="ml-3"
                            :src="require('@images/icons/form/error.svg')"
                            width="20"
                          />
                        </v-btn>
                      </li>

                      <li>
                        <v-btn
                          class="btn-no-bg text-capitalize pa-0 primary--text justify-start"
                          depressed
                          text
                          :ripple="false"
                          @click="onGoToBookmark('#vehicles')"
                        >
                          <v-img
                            class="mr-4"
                            :src="require('@images/icons/loads/vehicles.svg')"
                            width="20"
                          />
                          <span :class="{ 'error--text': !vehiclesForm.valid }">
                            Vehicles
                          </span>
                          <v-img
                            v-if="!vehiclesForm.valid"
                            class="ml-3"
                            :src="require('@images/icons/form/error.svg')"
                            width="20"
                          />
                        </v-btn>
                      </li>
                    </ul>
                  </panel-dropdown>
                </div>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-container>
      <button-save
        :on-click="submitCreateForm"
        :loading="isCreateLoadButtonLoading"
      >
        Create
      </button-save>
    </template>
  </base-layout>
</template>

<script>
import axios from 'axios';
import debounce from 'debounce';
import {
  minLength,
  required,
  cyrillic,
  decimal,
  alpha,
  email,
  lengthValidator,
  alphanumeric,
} from '@utils/helpers/validationRules';
import { mask } from 'vue-the-mask';
import PanelDropdown from '@components/Loads/PanelDropdown/PanelDropdown.vue';
import { createNamespacedHelpers } from 'vuex';
import { actions as dictionariesActions } from '@/store/dictionaries/constants';
import { actions as dispatchersActions } from '@modules/dispatchers/constants';
import { actions as driverActions } from '@modules/drivers/constants';
import { actions as loadActions } from '@modules/loads/constants';
import BaseLayout from '@components/BaseLayout';
import ButtonBack from '@components/LoadEdit/ButtonBack';
import ButtonSave from '@components/LoadEdit/ButtonSave';
import BaseFieldAutocomplete from '@components/BaseFieldAutocomplete';
import BaseFieldDatepicker from '@components/BaseFieldDatepicker';
import BaseFieldTextarea from '@components/BaseFieldTextarea';
import BaseFieldInput from '@components/BaseFieldInput';

const {
  mapActions: mapDictionariesActions,
  mapState: mapDictionariesState,
} = createNamespacedHelpers('dictionaries');
const {
  mapActions: mapDriversActions,
  mapState: mapDriversState,
} = createNamespacedHelpers('drivers');
const {
  mapActions: mapDispatchersActions,
  mapState: mapDispatchersState,
} = createNamespacedHelpers('dispatchers');
const { mapActions: mapLoadActions } = createNamespacedHelpers('loads');

export default {
  name: 'LoadCreate',
  components: {
    PanelDropdown,
    BaseFieldAutocomplete,
    BaseFieldDatepicker,
    BaseFieldTextarea,
    BaseFieldInput,
    ButtonBack,
    BaseLayout,
    ButtonSave,
  },
  directives: {
    mask,
  },
  data: () => ({
    newLoadId: null,
    isCreateLoadButtonLoading: false,
    generalInformationForm: {
      values: {},
      valid: false,
      loadId: {
        loading: false,
        checked: false,
        invalid: true,
        error: '',
      },
    },
    pickUpInformationForm: {
      values: {},
      valid: false,
    },
    deliveryInformationForm: {
      values: {},
      valid: false,
    },
    paymentInformationForm: {
      values: {
        delivery_payment: 0,
        pickup_payment: 0,
        carrier_fee: 0,
        broker_fee: 0,
      },
      valid: false,
    },
    shipperCastomerInformationForm: {
      values: {},
      valid: false,
    },
    vehiclesForm: {
      valid: false,
      values: [],
      initialValues: [],
    },
  }),
  computed: {
    ...mapDriversState(['drivers']),
    ...mapDispatchersState(['dispatchers']),
    ...mapDictionariesState(['states', 'paymentMethods', 'vehicles']),
  },
  methods: {
    checkId: debounce(
      function bounce() {
        if (!this.generalInformationForm.values.load_id) {
          this.generalInformationForm.loadId = {
            loading: false,
            checked: false,
            invalid: true,
            error: '',
          };

          return;
        }

        this.generalInformationForm.loadId.loading = true;
        this.generalInformationForm.loadId.checked = false;
        axios.get(`/loads/verify?load_id=${this.generalInformationForm.values.load_id}`)
          .then((response) => {
            if (this.generalInformationForm.values.load_id) {
              this.generalInformationForm.loadId.invalid = !response.data.unique;
            } else {
              this.generalInformationForm.loadId.invalid = true;
            }
          })
          .catch(() => {
            this.generalInformationForm.loadId.invalid = true;
          })
          .finally(() => {
            this.generalInformationForm.loadId.loading = false;
            this.generalInformationForm.loadId.checked = true;

            if (this.generalInformationForm.loadId.checked && this.generalInformationForm.loadId.invalid) {
              this.generalInformationForm.loadId.error = 'Load id isn\'t unique';
            } else if (this.generalInformationForm.loadId.checked && !this.generalInformationForm.loadId.invalid) {
              this.generalInformationForm.loadId.error = '';
            }
          });
      },
      250,
    ),
    hanldePriceInput(form, key) {
      const value = this[form].values[key];
      if (value === null || value === undefined) return false;
      const isValid = this.decimal(null)(value);
      const hasDot = value.indexOf('.') !== -1;

      if (isValid === true && hasDot) {
        const charsAfterDot = value.length - value.indexOf('.') - 1;

        if (charsAfterDot > 2) {
          this[form].values[key] = value.slice(0, value.length - 1);
        }
      }

      return false;
    },
    ...mapLoadActions({
      createLoad: loadActions.CREATE_LOAD,
      addLoadVehicles: loadActions.ADD_LOAD_VEHICLES,
      createMake: loadActions.CREATE_MAKE,
      createModel: loadActions.CREATE_MODEL,
    }),
    ...mapDriversActions({
      getDrivers: driverActions.GET_DRIVERS,
    }),
    ...mapDispatchersActions({
      getDispatchers: dispatchersActions.GET_DISPATCHERS,
    }),
    ...mapDictionariesActions({
      getPaymentMethods: dictionariesActions.GET_PAYMENT_METHODS,
      getVehicles: dictionariesActions.GET_VEHICLES,
      getStates: dictionariesActions.GET_STATES,
    }),
    minLength,
    required,
    cyrillic,
    decimal,
    alpha,
    email,
    alphanumeric,
    lengthValidator,
    handlePaymentInput(erase) {
      this.paymentInformationForm.values[`${erase}_payment`] = 0;
    },
    onAddRowVehiclesTable() {
      this.vehiclesForm.values.push({
        fields: {
          inop: false,
          year: null,
          make: null,
          model: null,
          type: null,
          color: null,
          plate: null,
          vin: null,
          lot: null,
          additional_info: null,
        },
        autocompletes: {
          makes: {
            data: [],
            search: '',
            value: null,
            prevSearch: '',
          },
          models: {
            data: [],
            search: '',
            value: null,
            prevSearch: '',
          },
        },
        new: true,
      });
    },
    handleMakesAutocompleteChange(search, index) {
      this.getLoadVehiclesMakes(search, index);
    },
    handleModelsAutocompleteChange(search, index) {
      this.getLoadVehiclesModels(search, index);
    },
    handleVehicleMakeChange(index, value) {
      const makeName = this.vehiclesForm.values[index].autocompletes.makes.data.find((make) => make.id === value).name;
      this.vehiclesForm.values[index].fields.model = null;
      this.vehiclesForm.values[index].fields.make_name = makeName;
    },
    handleVehicleModelChange(index, value) {
      const modelName = this.vehiclesForm.values[index].autocompletes.models.data.find((model) => model.id === value).name;
      this.vehiclesForm.values[index].fields.model_name = modelName;
    },
    handleVehicleDelete(id, index) {
      if (id) {
        this.deleteLoadVehicle({ loadId: this.id, id })
          .then(() => {
            this.vehiclesForm.values = [...this.vehiclesForm.values.slice(0, index), ...this.vehiclesForm.values.slice(index + 1)];
          }).catch(() => {
            this.$notificationError('Vehicle wasn\'t deleted');
          });
      } else {
        this.vehiclesForm.values = [...this.vehiclesForm.values.slice(0, index), ...this.vehiclesForm.values.slice(index + 1)];
      }
    },
    onGoToBookmark(id) {
      if (this.$el.querySelector(id)) {
        this.$vuetify.goTo(id, { offset: 20 })
          .then(() => this.$el.querySelector(id).classList.add('highlight-bookmark-block'))
          .then(
            setTimeout(() => this.$el.querySelector(id).classList.remove('highlight-bookmark-block'), 2000),
          );
      }
    },
    handleVehicleTypeChange(index, value) {
      const typeName = this.vehicles.data.find((type) => type.id === value).name;
      this.vehiclesForm.values[index].fields.type_name = typeName;
    },
    getLoadVehiclesMakes: debounce(
      function bounce(search = '', index) {
        if (this.vehiclesForm.values[index].autocompletes.makes.prevSearch !== search) {
          this.vehiclesForm.values[index].autocompletes.makes.search = search;
          axios.get(`/vehicles/makes?name=${search || ''}`, { requestName: 'vehicle makes' })
            .then(({ data }) => {
              this.vehiclesForm.values[index].autocompletes.makes.data = data.results;
              this.vehiclesForm.values[index].autocompletes.makes.next_page_url = data.next;
            }).then(() => {
              this.vehiclesForm.values[index].autocompletes.makes.prevSearch = search;
            });
        }
      },
      500,
    ),
    getLoadVehiclesModels: debounce(
      function bounce(search = '', index) {
        const makeId = this.vehiclesForm.values[index].fields.make;

        if ((this.vehiclesForm.values[index].autocompletes.models.prevSearch !== search || search === '') && makeId) {
          this.vehiclesForm.values[index].autocompletes.models.search = search;
          axios.get(`/vehicles/models?make=${makeId}&name=${search || ''}`, { requestName: 'vehicle models' })
            .then(({ data }) => {
              this.vehiclesForm.values[index].autocompletes.models.data = data.results;
              this.vehiclesForm.values[index].autocompletes.models.next_page_url = data.next;
            }).then(() => {
              this.vehiclesForm.values[index].autocompletes.models.prevSearch = search;
            });
        }
      },
      500,
    ),
    handleValidation(form) {
      const valid = form.validate();
      if (!valid) {
        this.$vuetify.goTo(form);
      }
      return valid;
    },
    submitCreateForm() {
      if (this.generalInformationForm.loadId.error) {
        this.$vuetify.goTo(this.$refs.generalInformationForm);
        return;
      }
      const isVehiclesFormValid = this.handleValidation(this.$refs.vehiclesForm);
      const isShipperCastomerInformationFormValid = this.handleValidation(this.$refs.shipperCastomerInformationForm);
      const isPaymentInformationFormValid = this.handleValidation(this.$refs.paymentInformationForm);
      const isDeliveryInformationFormValid = this.handleValidation(this.$refs.deliveryInformationForm);
      const isPickUpInformationFormValid = this.handleValidation(this.$refs.pickUpInformationForm);
      const isGeneralInformationFormValid = this.handleValidation(this.$refs.generalInformationForm);

      if (
        !isVehiclesFormValid
        || !isGeneralInformationFormValid
        || !isPickUpInformationFormValid
        || !isDeliveryInformationFormValid
        || !isPaymentInformationFormValid
        || !isShipperCastomerInformationFormValid
      ) {
        this.$notificationError('Incorrect load information. Please check your inputs.');
        return;
      }

      const load = {
        ...this.generalInformationForm.values,
        ...this.pickUpInformationForm.values,
        ...this.deliveryInformationForm.values,
        ...this.paymentInformationForm.values,
        ...this.shipperCastomerInformationForm.values,
      };

      this.isCreateLoadButtonLoading = true;
      this.createLoad({ data: load })
        .then((response) => {
          this.newLoadId = response.data.load_id;
          this.$notificationSuccess(`Load with id ${response.data.load_id} created`);
          if (this.vehiclesForm.values.length) {
            const newVehicles = this.vehiclesForm.values.filter((vehicle) => vehicle.new)
              .map(({ fields }) => ({ ...fields }));

            if (newVehicles.length) {
              this.addLoadVehicles({ id: response.data.load_id, data: newVehicles })
                .then(() => {
                  this.$notificationSuccess(`Load ${response.data.load_id} was updated with new vehicles`);
                })
                .catch(() => {
                  this.$notificationError(`Load ${response.data.load_id} was not updated with new vehicles`);
                });
            }
          }
        })
        .catch(() => {
          this.$notificationError('Load was not created');
        })
        .finally(() => {
          this.isCreateLoadButtonLoading = false;
          if (this.newLoadId) {
            this.$router.push({ name: this.$constants.ROUTE_NAMES.LOAD, params: { id: this.newLoadId } });
          }
        });
    },
    fetchNextPortionMakes: debounce(
      function bounce(url, index) {
        axios.get(url, { requestName: 'vehicle makes' })
          .then(({ data }) => {
            this.vehiclesForm.values[index].autocompletes.makes.data = this.vehiclesForm.values[index].autocompletes.makes.data.concat(data.results);
            console.log(this.vehiclesForm.values[index].autocompletes.makes.data);
            this.vehiclesForm.values[index].autocompletes.makes.next_page_url = data.next;
          });
      },
      300,
    ),
    fetchNextPortionModels: debounce(
      function bounce(url, index) {
        axios.get(url, { requestName: 'vehicle models' })
          .then(({ data }) => {
            this.vehiclesForm.values[index].autocompletes.models.data = this.vehiclesForm.values[index].autocompletes.models.data.concat(data.results);
            this.vehiclesForm.values[index].autocompletes.models.next_page_url = data.next;
          });
      },
      300,
    ),
    getNextPortionMakes(idx) {
      this.fetchNextPortionMakes(this.vehiclesForm.values[idx].autocompletes.makes.next_page_url, idx);
    },
    getNextPortionModels(idx) {
      this.fetchNextPortionModels(this.vehiclesForm.values[idx].autocompletes.models.next_page_url, idx);
    },
    addNewMake(index) {
      if (this.vehiclesForm.values[index].autocompletes.makes.data.length === 0) {
        this.createMake({ name: this.vehiclesForm.values[index].autocompletes.makes.search }).then((res) => {
          this.vehiclesForm.values[index].autocompletes.makes.data.push(res);
          this.handleVehicleMakeChange(index, res.id);
          this.vehiclesForm.values[index].fields.make = res.id;
          const input = this.$refs[`vehicleModel${index}`][0].$el.querySelector('input:not([type=hidden])');
          if (input) {
            setTimeout(() => {
              input.focus();
            }, 0);
          }
        });
      }
    },
    addNewModel(index) {
      if (this.vehiclesForm.values[index].autocompletes.models.data.length === 0) {
        this.createModel({
          name: this.vehiclesForm.values[index].autocompletes.models.search,
          make: this.vehiclesForm.values[index].fields.make,
        }).then((res) => {
          this.vehiclesForm.values[index].autocompletes.models.data.push(res);
          this.handleVehicleModelChange(index, res.id);
          this.vehiclesForm.values[index].fields.model = res.id;
          const input = this.$refs[`vehicleType${index}`][0].$el.querySelector('input:not([type=hidden])');
          if (input) {
            setTimeout(() => {
              input.focus();
            }, 0);
          }
        });
      }
    },
  },
  mounted() {
    this.getStates();
    this.getDrivers();
    this.getVehicles();
    this.getDispatchers();
    this.getPaymentMethods();
  },
};
</script>

